@import url(https://fonts.googleapis.com/css2?family=Shadows+Into+Light+Two&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family:'Shadows Into Light Two', cursive;
}

body {
  background: white;
  height: 100vh;
}

.todo-app {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 520px;
  min-height: 600px;
  background: #161a2b;
  text-align: center;
  margin: 128px auto;
  border-radius: 10px;
  padding-bottom: 32px;
}
.todo-input {
  padding: 14px 32px 14px 16px;
  border-radius: 10px;
  border: 2px solid pink;
  outline: none;
  width: 320px;
  background: white;
  color: black;
}

@media (max-width: 1024px) {
  .todo-app {
    width: 520px;
    min-height: 600px;
  }
}

@media (max-width: 768px) {
  .todo-app {
    width: 520px;
    min-height: 600px;
  }
}

@media (max-width: 425px) {
  .todo-app {
    width: 350px;
    min-height: 400px;
    margin: 50px auto;
  }
}
@media (max-width: 425px) {
  .todo-input {
    padding: 14px 10px 15px 10px;
    width: 250px;
  }
}

@media (max-width: 375px) {
  .todo-app {
    width: 300px;
    min-height: 400px;
    margin: 50px auto;
  }
}

@media (max-width: 375px) {
  .todo-input {
    padding: 14px 10px 15px 10px;
    width: 200px;
  }
}

h1 {
  margin: 32px 0;
  color: white;
  font-size: 30px;
  text-transform: uppercase;
}

.todo-form {
  margin-bottom: 32px;
}

.todo-button {
  padding: 16px;
  margin-left: 25px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  outline: none;
  background: linear-gradient(90deg,  #833ab4 0%, #fcb045 100%);
  color: #fff;
  text-transform: capitalize;
}

.todo-container {
  display: flex;
  flex-direction: row;
  position: relative;
}

.todo-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin:10px auto;
  color: #fff;
  padding: 16px;
  border-radius: 5px;
  width: 90%;
  background: #f7a1fa;
  background: linear-gradient(90deg, #833ab4 0%, #f7a1fa 50%, #fcb045 100%);
}
  
.icons {
  display: flex;
  align-items: center;
  font-size: 24px;
  cursor: pointer;
  color: white;
}






